.header {
   background-color: var(--color-bg);
   padding: 1rem 0;
}

.container {
   max-width: var(--container-max-width);
   margin: 0 auto;
   padding: 0 var(--side-padding);
   display: grid;
   grid-template-columns: 1fr auto 1fr;
   align-items: center;
   gap: 1rem;
   position: relative;
   z-index: 1000;
}

.left {
   display: flex;
   gap: 1.5rem;
   align-items: center;
}

.menuList {
   display: flex;
   gap: 1.5rem;
   list-style: none;
   padding: 0;
   margin: 0;
}

.menuList li a {
   text-decoration: none;
   color: var(--color-white);
   font-size: var(--font-md);
   letter-spacing: 0.03em;
   transition: color 0.3s ease;
}

.menuList li a:hover {
   color: var(--color-primary);
}

.logoWrapper {
   justify-self: center;
   display: flex;
   align-items: center;
}

.logo {
   height: clamp(50px, 8vw, 120px);
   object-fit: contain;
}

.right {
   display: flex;
   justify-content: flex-end;
   gap: 1rem;
   align-items: center;
}

.iconBtn {
   background: none;
   border: none;
   color: var(--color-white);
   font-size: 1.2rem;
   cursor: pointer;
   transition: color 0.3s ease;
   font-family: inherit;
   text-decoration: none;
   position: relative;
}

.iconBtn:hover {
   color: var(--color-primary);
}

.burgerBtn {
   display: none;
   background: none;
   border: none;
   font-size: 1.6rem;
   color: var(--color-white);
   cursor: pointer;
}

/* Мобильное меню */

.mobileMenu {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100vh;
   background-color: rgba(24, 24, 26, 0.95);
   z-index: 1000;
   display: flex;
   flex-direction: column;
   padding: 2rem 1.5rem;
   animation: fadeIn 0.3s ease-in-out;
}

.closeBtn {
   align-self: flex-end;
   background: none;
   border: none;
   font-size: 2rem;
   color: var(--color-white);
   cursor: pointer;
   margin-bottom: 2rem;
}

.mobileMenuList {
   list-style: none;
   display: flex;
   flex-direction: column;
   gap: 1.5rem;
   font-size: var(--font-lg);
   padding: 0;
}

.mobileMenuList li a {
   text-decoration: none;
   color: var(--color-white);
   font-weight: 500;
   letter-spacing: 0.03em;
   transition: color 0.3s ease;
}

.mobileMenuList li a:hover {
   color: var(--color-primary);
}

/* Медиазапросы */

/* На мобильных экранах показываем бургер и скрываем меню */

@media (max-width: 767px) {
   .container {
      grid-template-columns: 1fr auto 1fr;
      grid-template-rows: auto auto;
      gap: 0.5rem;
   }

   .menuList {
      display: none;
   }

   .burgerBtn {
      display: block;
   }

   .left {
      grid-column: 1 / 2;
   }

   .logoWrapper {
      grid-column: 2 / 3;
      justify-self: center;
   }

   .right {
      grid-column: 3 / 4;
      justify-content: flex-end;
      gap: 0.7rem;
   }

   .iconBtn {
      font-size: 1.4rem;
      padding: 0.3rem 0.6rem;
   }
}

/* На супер маленьких экранах 320-480 сужаем шрифты и расстояния */

@media (max-width: 480px) {
   .menuList li a {
      font-size: 0.85rem;
   }

   .right {
      gap: 0.4rem;
   }

   .iconBtn {
      font-size: 1.1rem;
      padding: 0.2rem 0.4rem;
   }

   .logo {
      height: 50px;
   }
}